import React from 'react';
import { Switch, Route} from "react-router-dom";
import "./style.scss"
import logo from "./vt-logo.png"
import LoginPage from "./LoginPage/LoginPage";
import MainPage from "./MainPage/MainPage";
import RegisterPage from "./RegisterPage/RegisterPage";


class App extends React.Component{
    render() {
        return (
            <div className="App">
                <Header/>
                <Switch>
                    <Route exact path="/login" component={LoginPage}/>
                    <Route exact path="/register" component={RegisterPage}/>
                    <Route path="/" component={MainPage}/>
                </Switch>
            </div>
        );
    }
}


const Header = ()=>{
    return(<div className="head">
        <span id="head-title">
            Лабораторная работа №4. Вариант 2180
        </span>
        <a href="http://se.ifmo.ru" target="_blank" rel="noopener noreferrer"> <img src={logo} alt="logo"/></a><br/>
        <span id="head-author">
            Выполнил студент группы P3214 Гораш Вячеслав Игоревич
   </span>
    </div>)
};


export default App;
